@import "../../index.scss";

.app__certs {
    flex: 1;
    width: 100%;
    flex-direction: column;
    margin-top: 20px;
}

.app__cert-filter {
    @include themify($themes) {

        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;

        margin: 4rem 0 2rem;

        .app__cert-filter-item {
            padding: 0.5rem 1rem;
            border-radius: 0.5rem;
            // background-color: #fff;
            background-color: themed("bg");
            // color: #000;
            color: themed("textColor");
            font-weight: 800;

            cursor: pointer;
            transition: all 0.3s ease;
            margin: 0.5rem;

            &:hover {
                background-color: var(--secondary-color);
                color: #fff;
            }

            @media screen and (min-width: 2000px) {
                padding: 1rem 2rem;
                border-radius: 0.85rem;
            }
        }

        .item-active {
            background-color: var(--secondary-color);
            color: #fff;
        }
    }
}

.app__cert-portfolio {
    @include themify($themes) {

        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;

        .app__cert-item {
            width: 270px;
            flex-direction: column;

            margin: 2rem;
            padding: 1rem;
            border-radius: 0.5rem;
            background-color: #fff;
            color: #000;

            cursor: pointer;
            transition: all 0.3s ease;

            &:hover {
                box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
            }

            @media screen and (min-width: 2000px) {
                width: 470px;
                padding: 1.25rem;
                border-radius: 0.75rem;
            }

            @media screen and (max-width: 300px) {
                width: 100%;
                margin: 1rem;
            }
        }
    }
}

.app__cert-img {
    @include themify($themes) {

        width: 100%;
        height: 230px;

        position: relative;

        img {
            width: 100%;
            height: 100%;
            border-radius: 0.5rem;
            object-fit: cover;
        }

        @media screen and (min-width: 2000px) {
            height: 350px;
        }
    }
}

.app__cert-hover {
    @include themify($themes) {

        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);

        border-radius: 0.5rem;
        opacity: 0;
        transition: all 0.3s ease;

        div {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.5);
            color: #fff;

            margin: 1rem;
            font-family: var(--font-base);
            font-weight: 800;
            cursor: pointer;
            transition: all 0.3s ease;

            svg {
                width: 50%;
                height: 50%;
                color: var(--white-color);

            }
        }
    }
}

.app__cert-content {
    @include themify($themes) {
        padding: 0.5rem;
        width: 100%;
        position: relative;
        flex-direction: column;

        h4 {
            margin-top: 1rem;
            line-height: 1.5;
            color: var(--secondary-color);
            text-align: center;

            @media screen and (min-width: 2000px) {
                margin-top: 3rem;
            }
        }

        p {
            color: var(--gray-color);
            text-align: justify;

        }

        .app__cert-tag {
            position: absolute;
            padding: 0.5rem 1rem;
            border-radius: 10px;
            background-color: #fff;
            top: -25px;
            color: #000;
        }
    }
}